import { Layout, Menu, Breadcrumb } from 'antd';
import React, { useState, useEffect } from "react";
import '../components/styles.css';

import DB from '../model/Database'


const CourseCategories = DB.CourseCategories;

const { SubMenu } = Menu;
const { Header, Content, Sider } = Layout;


export const CourseCategoriesView = () => {

/* Need to be define when using database */

/* inside dbState
const dbState = {
    data: null,
    isLoading: false,
    isLoaded: false,
    errCode : true,
};
*/
    const [state, setState] =
        useState({});

    const [dbState, dispatch] = DB.useReducer();
 
    useEffect(() => {
        CourseCategories.getCategories(dispatch);
    }, []);


    useEffect(() => {
        setState(dbState)
    }, [dbState]);
    
/*-----------------------------------------*/
    const print = () => {
        /*
        var values  = categories.map((item) => {
            
            var value = Object.values(item)
            return value
        }); */
        console.log(state.data)
    }

    const createIUObj = (jsonArray) => {

        if (jsonArray == null)
            return null

        let tmp = {}
        let id = 0
        let keys = []
        let uiObj = jsonArray.map(item => {
            id++;
            keys = Object.keys(item)
            tmp = { key: id.toString(), txt: item[keys[0]] }
            return tmp;
        })


        return uiObj;
    }

    const uiObjList = state.data ?
        createIUObj(state.data).map(item => {
            return (
                <SubMenu key={item.key} title={item.txt}>
                    {    /*
            <Menu.Item key="1">option1</Menu.Item>
            <Menu.Item key="2">option2</Menu.Item>
            <Menu.Item key="3">option3</Menu.Item>
            <Menu.Item key="4">option4</Menu.Item>
            */}

                </SubMenu>
            )
        }) : null



    return (
        <div>
            {state.isLoaded &&
                <Sider width={200} className="site-layout-background">

                    <Menu
                        mode="inline"
                        defaultSelectedKeys={['1']}
                        defaultOpenKeys={['sub1']}
                        style={{ height: '100%', borderRight: 0 }}
                    >
                        {
                            uiObjList
                        }
                    </Menu>
                </Sider>
            }
        </div>)

}
