import { Layout, Menu, Breadcrumb } from 'antd';
import { UserOutlined, LaptopOutlined, NotificationOutlined } from '@ant-design/icons';

import './styles.css';


import { Input, Space } from 'antd';
import { AudioOutlined } from '@ant-design/icons';


import {CourseCategoriesView} from '../views/CourseCategories'

import React, { useEffect, useContext, useState } from "react";
const { SubMenu } = Menu;
const { Header, Content, Sider } = Layout;

const { Search } = Input;



const suffix = (
    <AudioOutlined
        style={{
            fontSize: 16,
            color: '#1890ff',
        }}
    />
);

const onSearch = value => console.log(value);

const handleClick = e => {
    console.log('click ', e);
};

const SideMenuLayout = () => {
    
    const items = [
        {
            key: '1',
            txt: 'Course Catalog',
        },
        {
            key: '2',
            txt: 'Course Categories',
        },
        {
            key: '3',
            txt: 'Learning Pathways',
        },

    ]

    return (
        <Layout>
            <Header className="header">
                <div className="logo" />
                <Menu
                    theme="dark"
                    mode="horizontal"
                    onClick={handleClick}
                    defaultSelectedKeys={['2']}
                >
                    {
                        items.map(item => {
                            return (
                                <Menu.Item key={item.key}>{item.txt}</Menu.Item>
                            )
                        })
                    }

                    <Menu.Item key="4">
                        <Search placeholder="course code / keywords " allowClear onSearch={onSearch} style={{ width: 300, marginTop: 18 }} />
                    </Menu.Item>

                </Menu>

            </Header>
            <Layout>
                <CourseCategoriesView/>
                <Layout style={{ padding: '0 24px 24px' }}>
                    {/*
                    <Breadcrumb style={{ margin: '16px 0' }}>
                        <Breadcrumb.Item>Home</Breadcrumb.Item>
                        <Breadcrumb.Item>List</Breadcrumb.Item>
                        <Breadcrumb.Item>App</Breadcrumb.Item>
                    </Breadcrumb>
                    */}
                    <Content
                        className="site-layout-background"
                        style={{
                            padding: 24,
                            margin: 0,
                            height: 500,
                        }}
                    >
                        Content
                    </Content>
                </Layout>
            </Layout>
        </Layout>
    );
};

export default SideMenuLayout;